import './Buttonlist.scss'
import { Button } from "@mui/material";
import { FC } from "react";
import { IButton } from "../../types/types";

interface ButtonListProps {
	buttons: IButtonData,
	memoryButtons: IButton[],
	action: (button: IButton) => void
}
interface IButtonData {
	first: IButton[],
	second: IButton[]
}

const ButtonList: FC<ButtonListProps> = ({ buttons, memoryButtons, action }) => {

	return (
		<>
			<div className="calculator__container calculator__container_first">
				{memoryButtons.map((memoryButtonElement, index) =>
					<div className="MemoryButtons">

						<Button
							variant="contained"
							color={memoryButtonElement.color}
							key={index}
							onClick={() => action(memoryButtonElement)}

						>
							{memoryButtonElement.value}
						</Button>

					</div>
				)}
				{buttons.first.map((buttonElement, index) =>
					<Button
						variant="contained"
						color={buttonElement.color}
						key={index}
						onClick={() => action(buttonElement)}
					>
						{buttonElement.value}
					</Button>
				)}
			</div>
			<div className="calculator__container calculator__container_second">
					<Button
						
						variant="contained"
						color={'primary'}
						className='scroll-button'
						//onClick={() => action(buttonElement)}
					>
						;;
					</Button>
				{buttons.second.map((buttonElement, index) =>
					<Button
						variant="contained"
						color={buttonElement.color}
						key={index}
						onClick={() => action(buttonElement)}
					>
						{buttonElement.value}
					</Button>
				)}
			</div>
		</>
	);
}

export default ButtonList
