import ButtonList from './components/Buttonlist/Buttonlist'
import { buttonsData, memoryButtonsData } from './data/buttonsData'
import { ThemeProvider, createTheme } from '@mui/material/styles'
import CssBaseline from '@mui/material/CssBaseline'
import { useRef, useState } from 'react'

import './scss/index.sass'
import { Button, TextField } from '@mui/material'
import { useEffect } from 'react'
import { parseJsonSourceFileConfigFileContent } from 'typescript'
import MemoryButtons from './components/MemoryButtons/MemoryButtons'
import { IButton } from './types/types'

const darkTheme = createTheme({
	palette: {
		mode: 'dark',
	},
})

function App() {
	//  
	const [res, setRes] = useState('0')
	const [resPreview, setResPreview] = useState('')
	//   
	const [prevButton, setPrevButton] = useState<IButton>({
		id: NaN,
		value: '',
		type: '',
		color: '',
	})

	//    
	function action(button: IButton) {
		setPrevButton(button)
		const value = button.innerValue || button.value

		//   
		function doCalc(expression: string) {
			try {
				if (eval(expression))
					setRes(eval(expression))
				else setRes('')
			} catch (error) {
				setRes('')
			}
		}
		
		function showResPreview() {
			try {
				setResPreview(eval(res))
			} catch (err) {
				
			}
		}

		if (res === '') {
			setRes('0')
			return
		}
		switch (button.type) {
			case 'int':
				res === '0'
					? setRes(value)
					: prevButton.type === 'int' && value === ')'
						? setRes(res + `${value}*`)
						: setRes(res + value)
				break
			case 'const':
				res === '0'
					? setRes(value)
					: prevButton.type === 'const' && value === ')'
						? setRes(`*${value}`)
						: setRes(res + value)
				break
			case 'oper':
				prevButton.type === 'oper'
					? setRes(res.slice(0, -1) + value)
					: setRes(res + value)
				break
			case 'math':
				res === '0'
					? console.log(1)
					: prevButton.type === 'oper'
						? doCalc(String(button.mathValue).replaceAll('${res}', res.slice(0, -1)))
						: doCalc(String(button.mathValue).replaceAll('${res}', res))
				break
			case 'clear':
				res === '0'
					? console.log(0)
					: setRes(res.slice(0, -1))
				break
			case 'cls':
				setRes('0')
				break
			case '=':
				doCalc(res)
				break
			case 'M+':
				prevButton.type === 'oper'
					? localStorage.setItem('number', `${res.slice(0, -1)}`)
					: localStorage.setItem('number', `${res}`)
				break
			case 'M-':
				prevButton.type === 'oper'
					? localStorage.setItem('number', `-${res.slice(0, -1)}`)
					: localStorage.setItem('number', `-${res}`)
				break
			case 'MR':
				setRes(res + localStorage.getItem('number') || '')
				break
			case 'MC':
				localStorage.setItem('number', '')
				break
		}
		showResPreview()
	}
	//   ,    button.value
	window.onkeydown = e => keyBoardInput(e)
	function keyBoardInput(e: any) {

		interface IChange {
			from: string,
			to: string,
		}

		const changes: IChange[] = [
			{ from: 'Backspace', to: '' },
		]
		let key: string = ''

		changes.forEach(change =>
			key = e.key.replace(change.from, change.to).toLowerCase()
		)

		buttonsData.first.forEach(button => {
			if (key === button.value.toLowerCase()) action(button)
		})
	}
	
	return (
		<div className="App">
			<ThemeProvider theme={darkTheme}>
				<CssBaseline />
				<TextField
					label={resPreview}
					fullWidth
					sx={{
						marginLeft: '2px',
						marginRight: '2px'
					}}
					value={res}
					InputProps={{
						readOnly: true,
					}}
					variant="standard"
				/>
				<div className="calculator">

					<ButtonList
						action={action}
						memoryButtons={memoryButtonsData}
						buttons={buttonsData}
					/>
				</div>
			</ThemeProvider>
		</div>
	)

}

export default App
